<template>
<v-container class="mt-3">
  <v-stepper
    non-linear
    v-model="e1"
    class="mb-3"
  >
    <v-stepper-header>
      <v-stepper-step
        editable
        :complete="e1 > 1"
        step="1"
      >
        Estoque Inicial
      </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step
        editable
        :complete="e1 > 2"
        step="2"
      >
        Estoque Final
      </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step
        editable
        :complete="e1 > 3"
        step="3">
        Relatório de Vendas
      </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step
        editable
        step="4">
        Contagem do Estoque
      </v-stepper-step>
    </v-stepper-header>

    <v-stepper-items
      class="mb-3"
    >
      <v-stepper-content step="1">
        
        <v-row class="mt-3">
          <v-col>
            <v-form>
              <v-file-input
                label="Estoque Inicial"
                outlined
                dense
                truncate-length="50"
                accept=".xlsx"
                v-model="arquivoExcelEstoqueInicial"
              ></v-file-input>
            </v-form>
          </v-col>
          <v-col>
            <v-btn
              color="primary"
              @click="importarEstoque('inicial')"
            >
              IMPORTAR ARQUIVO
            </v-btn>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="text-center">
            <p class="mb-0" v-if="dadosEstoqueInicial">Data: {{ dadosEstoqueInicial.data }}</p>
            <p v-if="dadosEstoqueInicial">Hora: {{ dadosEstoqueInicial.hora }}</p>
            <v-data-table v-if="dadosEstoqueInicial"
              disable-pagination
              locale="pt"
              hide-default-footer
              :headers="headersEstoque"
              :items="dadosEstoqueInicial.estoque_itens"
              class="elevation-1"
              item-key="insumo.codigo_insumo"
            ></v-data-table>
          </v-col>
        </v-row>
      </v-stepper-content>

      <v-stepper-content step="2">
        <v-row class="mt-3">
          <v-col>
            <v-form>
              <v-file-input
                label="Estoque Final"
                outlined
                dense
                truncate-length="50"
                accept=".xlsx"
                v-model="arquivoExcelEstoqueFinal"
              ></v-file-input>
            </v-form>
          </v-col>
          <v-col>
            <v-btn
              color="primary"
              @click="importarEstoque('final')"
            >
              IMPORTAR ARQUIVO
            </v-btn>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="text-center">
            <p class="mb-0" v-if="dadosEstoqueFinal">Data: {{ dadosEstoqueFinal.data }}</p>
            <p v-if="dadosEstoqueFinal">Hora: {{ dadosEstoqueFinal.hora }}</p>
            <v-data-table v-if="dadosEstoqueFinal"
              disable-pagination
              locale="pt"
              hide-default-footer
              :headers="headersEstoque"
              :items="dadosEstoqueFinal.estoque_itens"
              class="elevation-1"
              item-key="insumo.codigo_insumo"
            ></v-data-table>
          </v-col>
        </v-row>
      </v-stepper-content>

      <v-stepper-content step="3">
        <v-row class="mt-3">
          <v-col>
            <v-form>
              <v-file-input
                label="Relatório de Vendas"
                outlined
                dense
                truncate-length="50"
                accept=".xlsx"
                v-model="arquivoExcelRelatorioVendas"
              ></v-file-input>
            </v-form>
          </v-col>
          <v-col>
            <v-btn
              color="primary"
              @click="importarRelatorioVendas()"
            >
              IMPORTAR ARQUIVO
            </v-btn>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="text-center">
            <p class="mb-0" v-if="dadosRelatorioVendas">{{ dadosRelatorioVendas.data }}</p>
            <v-data-table v-if="dadosRelatorioVendas"
              disable-pagination
              locale="pt"
              hide-default-footer
              :headers="headersRelatorioVendas"
              :items="dadosRelatorioVendas.vendas_itens"
              class="elevation-1"
              item-key="produto.codigo_produto"
            ></v-data-table>
          </v-col>
        </v-row>
      </v-stepper-content>

      <v-stepper-content step="4">
        <v-row class="mb-6">
          <v-col class="text-center">
            <v-data-table v-if="dadosContagemEstoque"
              disable-pagination
              locale="pt"
              hide-default-footer
              :headers="headersContagemEstoque"
              :items="dadosContagemEstoque"
              class="elevation-1"
              item-key="produto.codigo_produto"
            >
              <template v-slot:[`header.estoque_inicial`]="{ header }">
                <v-row>
                  <v-col class="pb-0">
                    {{ header.text }}
                    <p class="text-caption mb-0">(04/02/2021)</p>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col>Pct</v-col>
                  <v-col>Un</v-col>
                  <v-col>Total</v-col>
                </v-row>
              </template>

              <template v-slot:[`header.estoque_final`]="{ header }">
                <v-row>
                  <v-col class="pb-0">
                    {{ header.text }}
                    <p class="text-caption mb-0">(05/02/2021)</p>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col>Pct</v-col>
                  <v-col>Un</v-col>
                  <v-col>Total</v-col>
                </v-row>
              </template>
              
              <template v-slot:[`item.estoque_inicial`]="{ item }">
                <v-row>
                  <v-col style="background-color: lavender;">{{ item.estoque_inicial_pacotes }}</v-col>
                  <v-col style="background-color: lightblue;">{{ item.estoque_inicial_unidades }}</v-col>
                  <v-col style="background-color: bisque;">{{ item.estoque_inicial_total }}</v-col>
                </v-row>
              </template>

              <template v-slot:[`item.estoque_final`]="{ item }">
                <v-row>
                  <v-col style="background-color: lavender;">{{ item.estoque_final_pacotes }}</v-col>
                  <v-col style="background-color: lightblue;">{{ item.estoque_final_unidades }}</v-col>
                  <v-col style="background-color: bisque;">{{ item.estoque_final_total }}</v-col>
                </v-row>
              </template>

              <template v-slot:[`item.flag`]="{ item }">
                  <v-icon
                    :color="obterCor(item.diferencial, item.vendas)"
                  >
                    {{ item.diferencial === item.vendas ? 'mdi-thumb-up' : 'mdi-thumb-down' }}
                  </v-icon>
              </template>
            </v-data-table>
          </v-col>
        </v-row>

        <v-btn
          v-if="false"
          color="primary"
          @click="processarContagemEstoque"
        >
          PROCESSAR
        </v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</v-container>
</template>

<script>
  export default {
    name: 'ControleEstoque',

    data: () => ({
      e1: 1,
      arquivoExcelEstoqueInicial: null,
      dadosEstoqueInicial: null,
      
      arquivoExcelEstoqueFinal: null,
      dadosEstoqueFinal: null,

      arquivoExcelRelatorioVendas: null,
      dadosRelatorioVendas: null,

      dadosContagemEstoque: null,

      headersEstoque: [
        { text: 'Codigo', align: 'center', value: 'insumo.codigo_insumo', sortable: true },
        { text: 'Descrição', align: 'start', sortable: true, value: 'insumo.descricao' },
        { text: 'Pacotes', align: 'center', sortable: false, value: 'pacotes' },
        { text: 'Unidades', align: 'center', sortable: false, value: 'unidades' },
        { text: 'Total', align: 'center', sortable: false, value: 'total' }
      ],

      headersRelatorioVendas: [
        { text: 'Codigo', align: 'center', value: 'produto.codigo_produto', sortable: true },
        { text: 'Descrição', align: 'start', sortable: true, value: 'produto.descricao' },
        { text: 'Quantidade', align: 'center', sortable: false, value: 'quantidade' },
      ],

      headersContagemEstoque: [
        { text: 'Codigo', align: 'center', value: 'codigo_insumo', sortable: true },
        { text: 'Descrição', align: 'start', sortable: true, value: 'descricao' },
        { text: 'Estoque Inicial', align: 'center', sortable: false, value: 'estoque_inicial' },
        { text: 'Estoque Final', align: 'center', sortable: false, value: 'estoque_final' },
        { text: 'Diferencial', align: 'center', sortable: false, value: 'diferencial' },
        { text: 'Vendas', align: 'center', sortable: false, value: 'vendas' },
        { text: '', align: 'center', sortable: false, value: 'flag' },
      ]
    }),

    created() {
      this.dadosContagemEstoque = [
        {
          codigo_insumo: '1',
          descricao: 'Contra Filé',
          estoque_inicial_pacotes: 3,
          estoque_inicial_unidades: 2,
          estoque_inicial_total: 17,
          estoque_final_pacotes: 3,
          estoque_final_unidades: 2,
          estoque_final_total: 17,
          diferencial: 0,
          vendas: 0
        },
        {
          codigo_insumo: '2',
          descricao: 'Tiras de Alcatra',
          estoque_inicial_pacotes: 5,
          estoque_inicial_unidades: 14,
          estoque_inicial_total: 79,
          estoque_final_pacotes: 5,
          estoque_final_unidades: 11,
          estoque_final_total: 76,
          diferencial: 3,
          vendas: 3
        },
        {
          codigo_insumo: '3',
          descricao: 'Tiras de Frango',
          estoque_inicial_pacotes: 4,
          estoque_inicial_unidades: 2,
          estoque_inicial_total: 54,
          estoque_final_pacotes: 3,
          estoque_final_unidades: 14,
          estoque_final_total: 53,
          diferencial: 1,
          vendas: 1
        },
        {
          codigo_insumo: '4',
          descricao: 'Filé de Frango',
          estoque_inicial_pacotes: 8,
          estoque_inicial_unidades: 4,
          estoque_inicial_total: 100,
          estoque_final_pacotes: 7,
          estoque_final_unidades: 2,
          estoque_final_total: 86,
          diferencial: 14,
          vendas: 14
        },
        {
          codigo_insumo: '5',
          descricao: 'Bife Ancho',
          estoque_inicial_pacotes: 4,
          estoque_inicial_unidades: 3,
          estoque_inicial_total: 19,
          estoque_final_pacotes: 4,
          estoque_final_unidades: 2,
          estoque_final_total: 18,
          diferencial: 1,
          vendas: 0
        }
      ]
    },

    methods: {
      async importarEstoque(tipo) {
        const arquivoExcelEstoque = tipo === 'inicial' ? this.arquivoExcelEstoqueInicial : this.arquivoExcelEstoqueFinal

        const formData = new FormData()
        formData.append("file", arquivoExcelEstoque, arquivoExcelEstoque.name)

        const response = await fetch(`${process.env.VUE_APP_BASE_URL}/api/uploadestoque?tipo=${tipo}`, {
          method: 'POST',
          body: formData
        })

        if (tipo === 'inicial') {
          this.dadosEstoqueInicial = await response.json()
        }
        else {
          this.dadosEstoqueFinal = await response.json()        
        }
      },

      async importarRelatorioVendas() {
        const formData = new FormData()
        formData.append("file", this.arquivoExcelRelatorioVendas, this.arquivoExcelRelatorioVendas.name)

        const response = await fetch(`${process.env.VUE_APP_BASE_URL}/api/uploadrelatoriovendas`, {
          method: 'POST',
          body: formData
        })
        this.dadosRelatorioVendas = await response.json()
      },

      async processarContagemEstoque() {
        const response = await fetch(`${process.env.VUE_APP_BASE_URL}/api/processarcontagemestoque`)
        this.dadosContagemEstoque = await response.json()
      },

      obterCor(diferencial, vendas) {
        return diferencial === vendas ? 'green' : 'red'
      }
    }
  }
</script>